// =============================================================================
// Metrics Collector
// =============================================================================
// Collects and aggregates metrics for monitoring and observability
// In-memory metrics store (can be extended to Redis for distributed systems)

import { createLogger } from './logger';
import * as metricsDb from '@/db/metrics';

const logger = createLogger({ component: 'MetricsCollector' });

export interface Metric {
  name: string;
  value: number;
  timestamp: number;
  tags?: Record<string, string>;
}

export interface AggregatedMetric {
  name: string;
  count: number;
  sum: number;
  min: number;
  max: number;
  average: number;
  p50: number;
  p95: number;
  p99: number;
  tags?: Record<string, string>;
}

/**
 * Record a metric (async, uses database)
 */
export async function recordMetric(metric: Metric): Promise<void> {
  try {
    await metricsDb.recordMetric(metric);
  } catch (error) {
    logger.error('Failed to record metric', { metric, error });
  }
}

/**
 * Record multiple metrics at once (async, uses database)
 */
export async function recordMetrics(metrics: Metric[]): Promise<void> {
  try {
    await metricsDb.recordMetrics(metrics);
  } catch (error) {
    logger.error('Failed to record metrics', { count: metrics.length, error });
  }
}

/**
 * Get aggregated metrics for a time range (async, uses database)
 */
export async function getAggregatedMetrics(
  name: string,
  startTime?: number,
  endTime?: number,
  tags?: Record<string, string>
): Promise<AggregatedMetric | null> {
  return metricsDb.getAggregatedMetrics(name, startTime, endTime, tags);
  
  return {
    name,
    count,
    sum,
    min,
    max,
    average,
    p50,
    p95,
    p99,
    tags,
  };
}

/**
 * Get all metric names
 */
export function getMetricNames(): string[] {
  return Array.from(metricsStore.keys());
}

/**
 * Get recent metrics for a name
 */
export function getRecentMetrics(name: string, limit: number = 100): Metric[] {
  const metrics = metricsStore.get(name);
  if (!metrics) {
    return [];
  }
  
  return metrics.slice(-limit);
}

/**
 * Clear metrics (for testing or reset)
 */
export function clearMetrics(name?: string): void {
  if (name) {
    metricsStore.delete(name);
  } else {
    metricsStore.clear();
  }
}

/**
 * Helper functions for common metrics
 */

export async function recordRequestMetric(
  endpoint: string,
  method: string,
  statusCode: number,
  durationMs: number,
  tags?: Record<string, string>
): Promise<void> {
  await recordMetric({
    name: 'http.request',
    value: durationMs,
    timestamp: Date.now(),
    tags: {
      endpoint,
      method,
      statusCode: statusCode.toString(),
      ...tags,
    },
  });
  
  // Record status code count
  await recordMetric({
    name: 'http.request.count',
    value: 1,
    timestamp: Date.now(),
    tags: {
      endpoint,
      method,
      statusCode: statusCode.toString(),
      ...tags,
    },
  });
}

export async function recordPaymentMetric(
  endpointId: string,
  facilitator: string,
  amount: string,
  success: boolean,
  durationMs: number
): Promise<void> {
  await recordMetric({
    name: 'payment.verification',
    value: durationMs,
    timestamp: Date.now(),
    tags: {
      endpointId,
      facilitator,
      success: success.toString(),
    },
  });
  
  if (success) {
    await recordMetric({
      name: 'payment.amount',
      value: parseFloat(amount) / 1e6, // Convert atomic units to USDC
      timestamp: Date.now(),
      tags: {
        endpointId,
        facilitator,
      },
    });
  }
}

export async function recordErrorMetric(
  errorCode: string,
  endpoint?: string,
  tags?: Record<string, string>
): Promise<void> {
  await recordMetric({
    name: 'error.count',
    value: 1,
    timestamp: Date.now(),
    tags: {
      errorCode,
      endpoint: endpoint || 'unknown',
      ...tags,
    },
  });
}

export async function recordCircuitBreakerMetric(
  upstreamUrl: string,
  state: 'open' | 'closed' | 'half-open'
): Promise<void> {
  await recordMetric({
    name: 'circuit_breaker.state',
    value: state === 'open' ? 1 : 0,
    timestamp: Date.now(),
    tags: {
      upstreamUrl,
      state,
    },
  });
}

