// =============================================================================
// Authentication Middleware
// =============================================================================

import { NextRequest, NextResponse } from 'next/server';
import { findApiKeyByToken, updateApiKeyLastUsed, type ApiKey, type ApiKeyRole } from '@/db/api-keys';

export interface AuthenticatedRequest extends NextRequest {
  apiKey?: ApiKey;
}

/**
 * Extract API key from request headers
 * Supports:
 * - Authorization: Bearer <token> or <token>
 * - x-api-key: <token>
 */
export function extractApiKey(request: NextRequest): string | null {
  // First try x-api-key header (standard for API keys)
  const apiKeyHeader = request.headers.get('x-api-key');
  if (apiKeyHeader) {
    return apiKeyHeader.trim();
  }

  // Fall back to Authorization header
  const authHeader = request.headers.get('authorization');
  if (!authHeader) return null;

  // Support both "Bearer <token>" and "<token>" formats
  if (authHeader.startsWith('Bearer ')) {
    return authHeader.substring(7).trim();
  }
  return authHeader.trim();
}

/**
 * Authenticate request using API key
 */
export async function authenticateRequest(
  request: NextRequest
): Promise<{ apiKey: ApiKey } | { error: string; status: number }> {
  const token = extractApiKey(request);

  if (!token) {
    return {
      error: 'Authentication required',
      status: 401,
    };
  }

  // Validate token format (nf_live_... or nf_test_...)
  if (!token.startsWith('nf_live_') && !token.startsWith('nf_test_')) {
    return {
      error: 'Invalid API key format',
      status: 401,
    };
  }

  // Find API key in database
  const apiKey = await findApiKeyByToken(token);

  if (!apiKey) {
    return {
      error: 'Invalid API key',
      status: 401,
    };
  }

  // Check if key is expired
  if (apiKey.expiresAt && new Date(apiKey.expiresAt) < new Date()) {
    return {
      error: 'API key has expired',
      status: 401,
    };
  }

  // Extract IP address for tracking
  const ipAddress = request.ip || 
    request.headers.get('x-forwarded-for')?.split(',')[0]?.trim() || 
    request.headers.get('x-real-ip') || 
    undefined;

  // Update last used timestamp and IP (fire and forget)
  updateApiKeyLastUsed(apiKey.id, ipAddress).catch((err) => {
    console.error('[auth] Failed to update last used:', err);
  });

  return { apiKey };
}

/**
 * Check if API key has required role
 */
export function hasRole(apiKey: ApiKey, requiredRole: ApiKeyRole): boolean {
  const roleHierarchy: Record<ApiKeyRole, number> = {
    'read-only': 1,
    'user': 2,
    'admin': 3,
  };

  return roleHierarchy[apiKey.role] >= roleHierarchy[requiredRole];
}

/**
 * Require authentication middleware
 * Returns authenticated API key or error response
 */
export async function requireAuth(
  request: NextRequest,
  requiredRole: ApiKeyRole = 'user'
): Promise<
  | { apiKey: ApiKey; response?: never }
  | { apiKey?: never; response: NextResponse }
> {
  const authResult = await authenticateRequest(request);

  if ('error' in authResult) {
    return {
      response: NextResponse.json(
        {
          error: authResult.error,
          code: 'UNAUTHORIZED',
        },
        { status: authResult.status }
      ),
    };
  }

  // Check role
  if (!hasRole(authResult.apiKey, requiredRole)) {
    return {
      response: NextResponse.json(
        {
          error: 'Insufficient permissions',
          code: 'FORBIDDEN',
          details: `Required role: ${requiredRole}, your role: ${authResult.apiKey.role}`,
        },
        { status: 403 }
      ),
    };
  }

  return { apiKey: authResult.apiKey };
}

/**
 * Require any of the specified roles
 * Useful for routes that allow multiple role levels
 */
export async function requireAnyRole(
  request: NextRequest,
  allowedRoles: ApiKeyRole[]
): Promise<
  | { apiKey: ApiKey; response?: never }
  | { apiKey?: never; response: NextResponse }
> {
  const authResult = await authenticateRequest(request);

  if ('error' in authResult) {
    return {
      response: NextResponse.json(
        {
          error: authResult.error,
          code: 'UNAUTHORIZED',
        },
        { status: authResult.status }
      ),
    };
  }

  // Check if API key has any of the allowed roles
  const hasAllowedRole = allowedRoles.some(role => hasRole(authResult.apiKey, role));
  
  if (!hasAllowedRole) {
    return {
      response: NextResponse.json(
        {
          error: 'Insufficient permissions',
          code: 'FORBIDDEN',
          details: `Required one of roles: ${allowedRoles.join(', ')}, your role: ${authResult.apiKey.role}`,
        },
        { status: 403 }
      ),
    };
  }

  return { apiKey: authResult.apiKey };
}

