// =============================================================================
// API Keys Database Layer
// =============================================================================

import crypto from 'crypto';
import { getDb } from './client';

export type ApiKeyRole = 'admin' | 'user' | 'read-only';

export interface ApiKey {
  id: string;
  keyHash: string;
  name: string;
  role: ApiKeyRole;
  userId?: string;
  rateLimit: number;
  lastUsedAt?: string;
  lastUsedIp?: string; // IP address of last use
  expiresAt?: string;
  createdAt: string;
  updatedAt: string;
  revokedAt?: string;
  // x402 demo allowance tracking
  x402DemoCallsUsed?: number;
  x402DemoCallsLimit?: number;
  x402DemoAmountUsed?: string; // Stored as string for precision
  x402DemoAmountLimit?: string; // Stored as string for precision
}

export interface CreateApiKeyInput {
  name: string;
  role?: ApiKeyRole;
  userId?: string;
  rateLimit?: number;
  expiresAt?: string;
}

export interface ApiKeyWithToken extends Omit<ApiKey, 'keyHash'> {
  token: string; // Only returned on creation
}

/**
 * Generate a secure API key
 * 
 * Security: Uses 32 bytes (256 bits) of cryptographically secure randomness.
 * Format: nf_live_<base64url_encoded_random_bytes>
 * 
 * The prefix "nf_live_" is just for display/identification; the entropy
 * comes from the 32 random bytes, which when base64url encoded gives ~43 chars.
 */
export function generateApiKey(): string {
  // Use 32 bytes (256 bits) of entropy for strong security
  const randomBytes = crypto.randomBytes(32);
  // Base64url encoding of 32 bytes gives ~43 characters (no padding needed)
  const key = randomBytes.toString('base64url');
  return `nf_live_${key}`;
}

/**
 * Hash an API key for storage
 */
export function hashApiKey(key: string): string {
  return crypto.createHash('sha256').update(key).digest('hex');
}

/**
 * Create a new API key
 */
export async function createApiKey(input: CreateApiKeyInput): Promise<ApiKeyWithToken> {
  const db = getDb();
  const id = `key_${Date.now()}_${Math.random().toString(36).substring(2, 9)}`;
  const token = generateApiKey();
  const keyHash = hashApiKey(token);
  const now = new Date().toISOString();

  const apiKey: ApiKey = {
    id,
    keyHash,
    name: input.name,
    role: input.role || 'user',
    userId: input.userId,
    rateLimit: input.rateLimit || 1000,
    expiresAt: input.expiresAt,
    createdAt: now,
    updatedAt: now,
  };

  // Use adapter to insert
  await db.createApiKey(apiKey);

  return {
    ...apiKey,
    token, // Include the plain token only on creation
  };
}

/**
 * Find API key by hash
 */
export async function findApiKeyByHash(keyHash: string): Promise<ApiKey | null> {
  const db = getDb();
  return await db.findApiKeyByHash(keyHash);
}

/**
 * Find API key by token (validates and returns key)
 */
export async function findApiKeyByToken(token: string): Promise<ApiKey | null> {
  const keyHash = hashApiKey(token);
  return await findApiKeyByHash(keyHash);
}

/**
 * Update API key last used timestamp and IP address
 */
export async function updateApiKeyLastUsed(keyId: string, ipAddress?: string): Promise<void> {
  const db = getDb();
  await db.updateApiKeyLastUsed(keyId, ipAddress);
}

/**
 * Revoke an API key (soft delete)
 */
export async function revokeApiKey(keyId: string): Promise<boolean> {
  const db = getDb();
  return await db.revokeApiKey(keyId);
}

/**
 * List API keys for a user
 */
export async function listApiKeys(userId?: string): Promise<ApiKey[]> {
  const db = getDb();
  return await db.listApiKeys(userId);
}

/**
 * Update x402 demo allowance usage
 */
export async function updateApiKeyX402DemoAllowance(
  keyId: string,
  callsUsed: number,
  amountUsed: string
): Promise<void> {
  const db = getDb();
  await db.updateApiKeyX402DemoAllowance(keyId, callsUsed, amountUsed);
}

/**
 * Check if API key has remaining x402 demo allowance
 */
export function hasX402DemoAllowance(apiKey: ApiKey): {
  hasAllowance: boolean;
  callsRemaining: number;
  amountRemaining: string;
  reason?: string;
} {
  const callsUsed = apiKey.x402DemoCallsUsed || 0;
  const callsLimit = apiKey.x402DemoCallsLimit || 200;
  const amountUsed = BigInt(apiKey.x402DemoAmountUsed || '0');
  const amountLimit = BigInt(apiKey.x402DemoAmountLimit || '1000000');
  
  if (callsUsed >= callsLimit) {
    return {
      hasAllowance: false,
      callsRemaining: 0,
      amountRemaining: '0',
      reason: 'Calls limit reached',
    };
  }
  
  if (amountUsed >= amountLimit) {
    return {
      hasAllowance: false,
      callsRemaining: callsLimit - callsUsed,
      amountRemaining: '0',
      reason: 'Amount limit reached',
    };
  }
  
  return {
    hasAllowance: true,
    callsRemaining: callsLimit - callsUsed,
    amountRemaining: (amountLimit - amountUsed).toString(),
  };
}

/**
 * Update x402 demo limits for an API key
 */
export async function updateApiKeyX402DemoLimits(
  keyId: string,
  callsLimit?: number,
  amountLimit?: string
): Promise<void> {
  const db = getDb();
  await db.updateApiKeyX402DemoLimits(keyId, callsLimit, amountLimit);
}

/**
 * Find API key by ID (not hash)
 */
export async function findApiKeyById(keyId: string): Promise<ApiKey | null> {
  const allKeys = await listApiKeys();
  return allKeys.find(k => k.id === keyId) || null;
}

