// =============================================================================
// DATABASE QUERY HELPER
// =============================================================================
// Helper functions for executing raw SQL queries that work with both SQLite and PostgreSQL
// This is used for new tables that aren't yet in the adapter interface

import { getDb } from './client';

/**
 * Execute a raw SQL query (works with both SQLite and PostgreSQL)
 */
export async function executeQuery<T = any>(
  query: string,
  params: any[] = []
): Promise<T[]> {
  const db = getDb();
  
  // Check if it's PostgreSQL adapter
  if ('pool' in db && typeof (db as any).pool?.query === 'function') {
    const pool = (db as any).pool;
    const result = await pool.query(query, params);
    return result.rows;
  }
  
  // Check if it's SQLite adapter
  if ('db' in db && typeof (db as any).db?.prepare === 'function') {
    const dbInstance = (db as any).db;
    const stmt = dbInstance.prepare(query);
    return stmt.all(...params) as T[];
  }
  
  // Fallback: try direct access
  if (typeof (db as any).query === 'function') {
    const result = await (db as any).query(query, params);
    return result.rows || result;
  }
  
  if (typeof (db as any).prepare === 'function') {
    const stmt = (db as any).prepare(query);
    return stmt.all(...params) as T[];
  }
  
  throw new Error('Unable to execute query: database adapter not recognized');
}

/**
 * Execute a raw SQL query that returns a single row
 */
export async function executeQueryOne<T = any>(
  query: string,
  params: any[] = []
): Promise<T | null> {
  const results = await executeQuery<T>(query, params);
  return results.length > 0 ? results[0] : null;
}

/**
 * Execute a raw SQL query that doesn't return results (INSERT, UPDATE, DELETE)
 */
export async function executeUpdate(
  query: string,
  params: any[] = []
): Promise<{ rowCount?: number; changes?: number }> {
  const db = getDb();
  
  // Check if it's PostgreSQL adapter
  if ('pool' in db && typeof (db as any).pool?.query === 'function') {
    const pool = (db as any).pool;
    const result = await pool.query(query, params);
    return { rowCount: result.rowCount };
  }
  
  // Check if it's SQLite adapter
  if ('db' in db && typeof (db as any).db?.prepare === 'function') {
    const dbInstance = (db as any).db;
    const stmt = dbInstance.prepare(query);
    const result = stmt.run(...params);
    return { changes: (result as any).changes };
  }
  
  // Fallback
  if (typeof (db as any).query === 'function') {
    const result = await (db as any).query(query, params);
    return { rowCount: result.rowCount };
  }
  
  if (typeof (db as any).prepare === 'function') {
    const stmt = (db as any).prepare(query);
    const result = stmt.run(...params);
    return { changes: (result as any).changes };
  }
  
  throw new Error('Unable to execute update: database adapter not recognized');
}






