// =============================================================================
// Request Logging Helper
// =============================================================================
// Provides helpers for consistent request logging across all API routes

import { NextRequest, NextResponse } from 'next/server';
import { getOrCreateRequestId, createRequestLogger, addRequestIdToResponse } from './request-id';
import { type LogContext } from './logger';
import { setSentryContext, captureException } from './sentry';
import { extractApiKey } from './auth';
import { findApiKeyByToken } from '@/db/api-keys';

/**
 * Request context extracted from request
 */
export interface RequestContext {
  requestId: string;
  apiKeyId?: string;
  endpoint?: string;
  method: string;
  ip?: string;
  startTime: number;
}

/**
 * Extract request context for logging
 */
export async function extractRequestContext(
  request: NextRequest,
  endpoint?: string
): Promise<RequestContext> {
  const requestId = getOrCreateRequestId(request);
  const ip = request.ip || 
    request.headers.get('x-forwarded-for')?.split(',')[0]?.trim() || 
    request.headers.get('x-real-ip') || 
    undefined;
  
  // Try to get API key ID for context
  let apiKeyId: string | undefined;
  try {
    const token = extractApiKey(request);
    if (token) {
      const apiKey = await findApiKeyByToken(token);
      if (apiKey) {
        apiKeyId = apiKey.id;
      }
    }
  } catch {
    // Ignore errors when extracting API key (not critical for logging)
  }
  
  // Set Sentry context
  setSentryContext({
    requestId,
    apiKeyId,
    endpoint: endpoint || request.nextUrl.pathname,
    method: request.method,
  });
  
  return {
    requestId,
    apiKeyId,
    endpoint: endpoint || request.nextUrl.pathname,
    method: request.method,
    ip,
    startTime: Date.now(),
  };
}

/**
 * Log request completion
 */
export function logRequestCompletion(
  ctx: RequestContext,
  statusCode: number,
  error?: Error
) {
  const duration = Date.now() - ctx.startTime;
  const logger = createRequestLogger(ctx.requestId, {
    method: ctx.method,
    endpoint: ctx.endpoint,
    ip: ctx.ip,
  });
  
  const logContext: LogContext = {
    requestId: ctx.requestId,
    apiKeyId: ctx.apiKeyId,
    endpoint: ctx.endpoint,
    method: ctx.method,
    statusCode,
    durationMs: duration,
    ip: ctx.ip,
  };
  
  if (error) {
    logContext.error = error;
    logContext.errorCode = (error as any).code || 'UNKNOWN_ERROR';
    logContext.stack = error.stack;
    
    logger.error(logContext, `Request failed: ${ctx.method} ${ctx.endpoint}`);
    
    // Capture in Sentry
    captureException(error, {
      requestId: ctx.requestId,
      apiKeyId: ctx.apiKeyId,
      endpoint: ctx.endpoint,
    });
  } else {
    logger.info(logContext, `Request completed: ${ctx.method} ${ctx.endpoint}`);
  }
}

/**
 * Wrap response with request ID and logging
 */
export function wrapResponse(
  response: NextResponse,
  ctx: RequestContext,
  error?: Error
): NextResponse {
  // Add request ID to response
  addRequestIdToResponse(response, ctx.requestId);
  
  // Log completion
  logRequestCompletion(ctx, response.status, error);
  
  return response;
}

/**
 * Create error response with logging
 */
export function createErrorResponse(
  ctx: RequestContext,
  error: Error | string,
  statusCode: number = 500,
  code?: string
): NextResponse {
  const errorObj = error instanceof Error ? error : new Error(error);
  const errorCode = code || (errorObj as any).code || 'INTERNAL_ERROR';
  
  const response = NextResponse.json(
    {
      error: errorObj.message,
      code: errorCode,
      requestId: ctx.requestId,
    },
    { status: statusCode }
  );
  
  return wrapResponse(response, ctx, errorObj);
}







