// =============================================================================
// METRICS DATABASE
// =============================================================================
// Database operations for metrics storage

import { executeQuery, executeQueryOne, executeUpdate } from './query-helper';
import type { Metric, AggregatedMetric } from '@/lib/metrics-collector';

export interface MetricRow {
  id: string;
  name: string;
  value: string; // Stored as numeric in DB, but returned as string for precision
  timestamp: string; // BigInt in DB, string for JS
  tags: string | Record<string, string>; // JSONB in PostgreSQL, TEXT in SQLite
  created_at: string;
}

/**
 * Convert database row to Metric
 */
function rowToMetric(row: MetricRow): Metric {
  return {
    name: row.name,
    value: parseFloat(row.value),
    timestamp: parseInt(row.timestamp),
    tags: typeof row.tags === 'string' ? JSON.parse(row.tags) : row.tags,
  };
}

/**
 * Record a metric
 */
export async function recordMetric(metric: Metric & { id?: string }): Promise<void> {
  const id = metric.id || `m_${Date.now()}_${Math.random().toString(36).substring(2, 9)}`;
  const db = require('./client').getDb();
  const isPostgres = 'pool' in db || typeof (db as any).pool?.query === 'function';
  
  const query = isPostgres
    ? `INSERT INTO metrics (id, name, value, timestamp, tags, created_at)
       VALUES ($1, $2, $3, $4, $5, $6)`
    : `INSERT INTO metrics (id, name, value, timestamp, tags, created_at)
       VALUES (?, ?, ?, ?, ?, ?)`;
  
  const params = [
    id,
    metric.name,
    metric.value.toString(),
    metric.timestamp.toString(),
    JSON.stringify(metric.tags || {}),
    new Date().toISOString(),
  ];

  await executeUpdate(query, params);
}

/**
 * Record multiple metrics
 */
export async function recordMetrics(metrics: Metric[]): Promise<void> {
  // Batch insert for better performance
  for (const metric of metrics) {
    await recordMetric(metric);
  }
}

/**
 * Get metrics for a time range
 */
export async function getMetrics(
  name: string,
  startTime?: number,
  endTime?: number,
  tags?: Record<string, string>
): Promise<Metric[]> {
  const db = require('./client').getDb();
  const isPostgres = 'pool' in db || typeof (db as any).pool?.query === 'function';
  
  let query = 'SELECT * FROM metrics WHERE name = ';
  const params: any[] = [];
  let paramIndex = 1;

  if (isPostgres) {
    query += `$${paramIndex++}`;
  } else {
    query += '?';
  }
  params.push(name);

  if (startTime !== undefined) {
    if (isPostgres) {
      query += ` AND timestamp >= $${paramIndex++}`;
    } else {
      query += ' AND timestamp >= ?';
    }
    params.push(startTime.toString());
  }

  if (endTime !== undefined) {
    if (isPostgres) {
      query += ` AND timestamp <= $${paramIndex++}`;
    } else {
      query += ' AND timestamp <= ?';
    }
    params.push(endTime.toString());
  }

  // Tag filtering (simplified - for complex queries, consider using JSONB operators in PostgreSQL)
  if (tags && Object.keys(tags).length > 0) {
    // This is a simplified approach - for production, use proper JSONB queries
    query += ' AND tags IS NOT NULL';
  }

  query += ' ORDER BY timestamp DESC LIMIT 10000'; // Limit to prevent memory issues

  const rows = await executeQuery<MetricRow>(query, params);
  return rows.map(rowToMetric);
}

/**
 * Get aggregated metrics
 */
export async function getAggregatedMetrics(
  name: string,
  startTime?: number,
  endTime?: number,
  tags?: Record<string, string>
): Promise<AggregatedMetric | null> {
  const metrics = await getMetrics(name, startTime, endTime, tags);
  
  if (metrics.length === 0) {
    return null;
  }

  // Filter by tags if provided
  let filtered = metrics;
  if (tags) {
    filtered = metrics.filter(m => {
      if (!m.tags) return false;
      return Object.entries(tags).every(([key, value]) => m.tags![key] === value);
    });
  }

  if (filtered.length === 0) {
    return null;
  }

  // Calculate aggregations
  const values = filtered.map(m => m.value).sort((a, b) => a - b);
  const count = values.length;
  const sum = values.reduce((a, b) => a + b, 0);
  const min = values[0];
  const max = values[values.length - 1];
  const average = sum / count;

  // Calculate percentiles
  const p50Index = Math.floor(count * 0.5);
  const p95Index = Math.floor(count * 0.95);
  const p99Index = Math.floor(count * 0.99);

  return {
    name,
    count,
    sum,
    min,
    max,
    average,
    p50: values[p50Index] || average,
    p95: values[p95Index] || max,
    p99: values[p99Index] || max,
    tags: tags || filtered[0]?.tags,
  };
}

/**
 * Clean up old metrics (keep only recent ones)
 */
export async function cleanupOldMetrics(olderThanDays: number = 30): Promise<number> {
  const cutoffTime = Date.now() - (olderThanDays * 24 * 60 * 60 * 1000);
  const db = require('./client').getDb();
  const isPostgres = 'pool' in db || typeof (db as any).pool?.query === 'function';
  
  const query = isPostgres
    ? `DELETE FROM metrics WHERE timestamp < $1`
    : `DELETE FROM metrics WHERE timestamp < ?`;
  
  const result = await executeUpdate(query, [cutoffTime.toString()]);
  return result.rowCount ?? result.changes ?? 0;
}






